.App {
  text-align: center;

}

.ant-carousel .slick-slide {
  text-align: center;
  height: 280px;
  line-height: 160px;
  background: #e4e2fa;
  overflow: hidden;
}

.folio:hover {
  background-color: black;
}

.folio:hover span {
  color: #76d0a3;
}

.folio:hover img {
  width: 120px;
  height: 160px;
}

.gray:hover {
  background-color: #5a5a5a;
}

.App-intro {
  font-size: large;
}

  .logo {
      position: absolute;
      left:10px;
      width: 80px;
      height: 90px;
      background: url(assets/icon-enterprise.png) no-repeat;
      background-size: 100%;
      margin: 0 auto 50px;

    }

    .logo1 {
      display: block;
      margin: 0 auto;
        width: 80px;
        height: 90px;
        background: url(assets/design.png) no-repeat;
        background-size: 100%;
      }

      .logo2 {
        display: block;
        margin: 0 auto;
        opacity: 0.95;
          width: 80px;
          height: 90px;
          background: url(assets/care.png) no-repeat;
          background-size: 100%;
        }

        .logo3 {
          display: block;
          margin: 0 auto;
            width: 80px;
            height: 90px;
            background: url(assets/template.png) no-repeat;
            background-size: 100%;
          }

          .logo4 {
            display: block;
            margin: 0 auto;
              width: 350px;
              height: 300px;
              background: url(assets/yaounde.jpg) no-repeat;
              background-size: 100%;
            }

            .logo5 {
              display: block;
              margin: 0 auto;
              width: 340px;
              height: 290px;
                background: url(assets/douala.jpg) no-repeat;
                background-size: 100%;
              }

              .logo6 {
                display: block;
                margin: 0 auto;
                width: 340px;
                height: 340px;
                  background: url(assets/limbe.jpg) no-repeat;
                  background-size: 100%;
                }


          .border:hover{
            background-color: #e4e2fa;
          }

          .hover:hover{
            background-color: #e4e2fa;
          }

          .blue:hover{
            background-color: #76d0a3;
          }

          .fancy2a {
            border-width: 0 0 5px;
            border-style: solid;
            -webkit-border-image: url(http://www.w3.org/TR/css3-background/border.png) 27 stretch;
                 -o-border-image: url(http://www.w3.org/TR/css3-background/border.png) 27 stretch;
                    border-image: url(http://www.w3.org/TR/css3-background/border.png) 27 fill stretch;
          }

          .flotte {
            float: left;

            }

    .name {
      font-size: 30px;
      position: absolute;
      left:80px;
      width: 30px;
      height: 20px;
      background-size: 100%;
      margin: 0 auto 50px;

    }


    .top {
        position: absolute;
        top:0px;
      }

    .border {
      border-style: solid ;
      border-width: 2px;
      border-color: #e4e2fa;

      }

      .menu {
        border-style: solid ;
        border-width: 2px;
        border-color: #3db8c1;
        background-color: #fbfbfb;

        }

    .center {
          text-align: center;
        }

    .name0 {
        font-size: 30px;
        width: 30px;
        height: 20px;
      }

@media only screen and (min-width: 992px)  {

.query {
  border-left: solid  black ;
  border-width: 1px;
  border-color: #919191;
  padding-left: 20px;
  }

.font {
  font-size: 18px;
  }

}

@media only screen and (max-width: 991px) and (min-width: 100px)  {

.bottomborder {
  border-bottom: solid  black ;
  border-width: 1px;
  border-color: #919191;
}

.font {
  font-size: 14px;
  }

}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



